@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;700&display=swap);
.progress-bar{height:4px;width:100%;background-color:var(--color-gray-700);transition:all 0.15s ease;position:relative;display:flex;margin-top:0.5rem}.progress-bar-line{width:100%;height:100%;background-color:var(--color-primary-600);position:absolute;transition:width 0.25s}.progress-bar-container{margin:0.5rem;width:100%;height:8px;background-color:transparent}.progress-bar-container-progress{font-weight:600;font-size:0.875rem}.progress-bar-txt{margin:0.75rem 0;font-size:0.875rem}

.ocr-file-upload{margin:1rem 0 0.5rem;display:flex;align-items:center;justify-content:center;flex-direction:column;width:100%;background-color:var(--color-gray-800);padding:0 1rem;height:16rem;border-radius:2px;position:relative}.ocr-file-upload-container{display:flex;flex-direction:column;align-items:center;text-align:center}.ocr-file-upload-container-txt-title{font-size:1.125rem;color:var(--color-white);margin:0.625rem 0}.ocr-file-upload-container-txt-desc{color:var(--color-gray-100);margin:0;line-height:1.35;font-size:0.75rem}.ocr-file-upload .icon-file-upload,.ocr-file-upload .icon-file-upload-success{width:2rem;height:2rem;fill:var(--color-white)}.ocr-file-upload-input{cursor:pointer;opacity:0;position:absolute;height:100%;width:100%}

.image-wrapper{height:100%;border-radius:2px;background-color:var(--color-gray-800);width:100%;height:400px}.image-wrapper-text{height:100%;display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center;padding:0 2rem}.image-wrapper-text h3{font-size:1.125rem;color:var(--color-white);margin:0.625rem 0}.image-wrapper-text-desc{color:var(--color-gray-100);font-size:0.75rem}.image-wrapper-text .icon-image-preview{width:2rem;height:2rem;fill:var(--color-white)}.image-wrapper img{width:100%;height:100%;position:relative;font-size:0.875rem}

.ocr-by-link{width:100%;display:flex;flex-direction:column;margin:1rem 0 0.5rem}.ocr-by-link-input{font-family:var(--font);color:var(--color-white);background-color:var(--color-gray-800);border:none;border-radius:2px;padding:0.625rem 1rem;outline:none;font-size:0.875rem;caret-color:var(--color-primary-600);margin:0 0 1rem}.ocr-by-link-input:-ms-input-placeholder{color:var(--color-gray-200)}.ocr-by-link-input::placeholder{color:var(--color-gray-200)}.ocr-by-link-input:focus{box-shadow:0 0 2px var(--color-gray-200) inset}

.icon-button{outline:none;padding:0.625rem;border-radius:2px;transition:0.25s;display:flex;align-items:center;border:none;background-color:var(--color-gray-700);cursor:pointer}.icon-button:hover{background-color:var(--color-primary-600)}.icon-button svg{fill:var(--color-white);width:16px;height:16px}.icon-button-container{position:relative}.icon-button-container:hover .icon-button-tooltip{opacity:1;visibility:visible}.icon-button-tooltip{z-index:1;opacity:0;visibility:hidden;position:absolute;top:-2.5rem;right:calc(-50% - 3px);border-radius:1px;padding:0.375rem 0.75rem;font-size:0.75rem;font-family:var(--font);color:var(--color-white-two);background-color:var(--color-gray-500);border-radius:2px;white-space:nowrap;box-shadow:0 4px 8px var(--color-black-three)}.icon-button-tooltip::after{bottom:-6px;left:calc(50% - 6px);position:absolute;content:"";width:12px;height:6px;background-color:var(--color-gray-500);-webkit-clip-path:polygon(50% 100%, 0 0, 100% 0);clip-path:polygon(50% 100%, 0 0, 100% 0)}

.text-wrapper{display:flex;flex-direction:column;align-items:flex-end;width:100%;margin:0.5rem 0;position:relative;min-height:11rem}.text-wrapper-actions{display:flex;grid-gap:0.5rem;gap:0.5rem;margin-top:1rem}.text-wrapper-output{height:100%;width:100%;border-radius:1px;box-sizing:border-box;resize:none;font-family:var(--font);font-size:0.875rem}.text-wrapper-output:focus{outline:none}

.btn{padding:0.625rem 1.5rem;outline:none;border:none;font-family:var(--font);background:var(--color-primary-600);font-weight:600;font-size:0.875rem;border-radius:2px;cursor:pointer;white-space:nowrap;color:var(--color-white)}.btn:disabled{cursor:not-allowed}.btn:hover:not(:disabled){background-color:var(--color-primary-500)}@media screen and (max-width: 414px){.btn{width:100%}}

.languages{display:flex;align-items:center;grid-gap:1rem;gap:1rem;flex-wrap:wrap}.languages span{color:var(--color-gray-100);font-size:0.875rem}.option-active{font-weight:600;color:var(--color-white);background-color:var(--color-primary-600)}.languages-select{position:relative;cursor:pointer}.languages-select .selected-language{width:14rem;padding:0.625rem 1rem;border-radius:2px;background-color:var(--color-gray-800);display:flex;align-items:center;justify-content:space-between;color:var(--color-white);font-size:0.875rem}.languages-select .selected-language .caret-down{width:12px;height:6px;-webkit-clip-path:polygon(50% 100%, 0 0, 100% 0);clip-path:polygon(50% 100%, 0 0, 100% 0);background-color:var(--color-gray-300);position:relative}.languages-select ul{top:2.5rem;position:absolute;list-style:none;background-color:var(--color-gray-800);border-radius:2px;overflow-y:auto;margin:0;padding:0;width:14rem;height:11.5rem;z-index:1;box-shadow:0 6px 12px var(--color-black-three);font-size:0.875rem}.languages-select li{padding:0.625rem 1rem}@media screen and (max-width: 512px){.languages span{display:none}}@media screen and (max-width: 414px){.languages,.languages-select,.selected-language,ul{min-width:100%}}

.error{margin:1rem 0 0;display:flex;align-items:center;justify-content:space-between;font-size:1rem;width:100%;background-color:var(--color-primary-200);border-radius:2px;padding:0.625rem 1rem}.error-message{color:var(--color-primary-800);display:flex;align-items:center;grid-gap:0.5rem;gap:0.5rem;font-size:0.875rem}.error .icon-close{fill:var(--color-primary-800) !important;cursor:pointer}

.convert-options{position:relative;display:flex;align-self:flex-start;overflow:hidden;width:100%;border-bottom:1px solid var(--color-gray-700)}.convert-option{border:none;outline:none;background-color:transparent;font-family:var(--font);font-size:0.875rem;font-weight:600;color:var(--color-gray-100);padding:1rem;display:flex;align-items:center;justify-content:center;grid-gap:0.75rem;gap:0.75rem;text-align:center;cursor:pointer;position:relative;border-bottom:2px solid transparent}.convert-option:disabled{cursor:not-allowed}.active.convert-option{color:var(--color-white);border-bottom:2px solid var(--color-primary-600)}

.main-content{display:flex;flex-direction:column;align-items:center;justify-content:space-between;max-width:40rem;padding:0 2rem;margin:1rem auto;grid-gap:0.5rem;gap:0.5rem;height:100%}.convert-actions{width:100%;align-items:center;justify-content:space-between;display:flex;grid-gap:1rem;gap:1rem;flex-wrap:wrap}

body{margin:0;padding:0;color:hsl(0, 0%, 75%);color:var(--color-white-three);font-family:"Raleway", sans-serif;font-family:var(--font);background-color:hsl(0, 0%, 6%);background-color:var(--color-gray-900)}*{box-sizing:border-box}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{background:hsl(0, 0%, 10%);background:var(--color-gray-700)}::-webkit-scrollbar-thumb{background:hsl(0, 0%, 16%);background:var(--color-gray-500)}b{color:hsl(0, 0%, 95%);color:var(--color-white)}hr{border-color:hsl(0, 0%, 12%);border-color:var(--color-gray-600);border-top:0;margin:0;padding:0}:root{--color-hue: 200;--color-black: hsl(0, 0%, 0%);--color-black-two: hsl(0, 0%, 2%);--color-black-three: hsl(0, 0%, 4%);--color-white: hsl(0, 0%, 95%);--color-white-two: hsl(0, 0%, 85%);--color-white-three: hsl(0, 0%, 75%);--color-gray-900: hsl(0, 0%, 6%);--color-gray-800: hsl(0, 0%, 8%);--color-gray-700: hsl(0, 0%, 10%);--color-gray-600: hsl(0, 0%, 12%);--color-gray-500: hsl(0, 0%, 16%);--color-gray-400: hsl(0, 0%, 18%);--color-gray-300: hsl(0, 0%, 20%);--color-gray-200: hsl(0, 0%, 30%);--color-gray-100: hsl(0, 0%, 50%);--color-primary-800: hsl(var(--color-hue), 84%, 64%);--color-primary-700: hsl(var(--color-hue), 84%, 54%);--color-primary-600: hsl(var(--color-hue), 84%, 40%);--color-primary-500: hsl(var(--color-hue), 84%, 36%);--color-primary-200: hsl(var(--color-hue), 42%, 12%);--font: "Raleway", sans-serif}

